import { useTranslation } from 'react-i18next'
import { Typography } from 'components'
import theme from 'theme'

export default function Score({ playerIndex, round, isCurrentRound = false }) {
  const { t } = useTranslation()
  const roundIndex = round.index + 1
  if (round.scores.length === 0) {
    return <Typography variant='h3'>{`${roundIndex}. ${isCurrentRound ? '' : '0'}`}</Typography>
  } else if (round.scores[0].displaySymbol === 'BUST') {
    return <Typography variant='h3'>{`${roundIndex}. ${t('Bust ')}`}</Typography>
  } else {
    const score = round.scores.map(s => s.value).reduce((a, b) => a + b)
    // WORKAROUND: Don't understand that why cannot use className with some styles to override the color.
    // For example, add `color: props => theme.players[props.index] + '!important'` in the styles file.
    // This will cause a bug, which cause the App renders wrong color by unknown reasons after switching to the next player.
    // At that time, the player index is correct.
    return <Typography style={score >= 100 ? { color: `${theme.players[playerIndex]}` } : {}} variant='h3'>{`${roundIndex}. ${score}`}</Typography>
  }
}
